import { auth } from "~/server/auth";
import { getTopTracks } from "~/server/auth/lastfm";
import { db } from "~/server/db";

export const runtime = "nodejs";

type TimePeriod = "overall" | "7day" | "1month" | "3month" | "6month" | "12month";

/**
 * GET /api/music/top-tracks?period=overall&limit=10
 * Get the user's top tracks
 */
export async function GET(request: Request) {
	try {
		// Get session
		const session = await auth();

		if (!session?.user?.email) {
			return Response.json({ error: "Unauthorized" }, { status: 401 });
		}

		// Parse query parameters
		const { searchParams } = new URL(request.url);
		const period = (searchParams.get("period") || "overall") as TimePeriod;
		const limit = Math.min(parseInt(searchParams.get("limit") || "10"), 50);

		// Get user's account with Last.fm credentials
		const user = await db.user.findUnique({
			where: { email: session.user.email },
			include: { accounts: true },
		});

		if (!user) {
			return Response.json(
				{ error: "User not found" },
				{ status: 404 }
			);
		}

		const lastfmAccount = user.accounts.find(
			(account) => account.provider === "lastfm"
		);

		if (!lastfmAccount?.access_token) {
			return Response.json(
				{ error: "Last.fm not connected" },
				{ status: 400 }
			);
		}

		// Get top tracks
		const topTracks = await getTopTracks(
			lastfmAccount.access_token,
			period,
			limit
		);

		const tracks = (topTracks.toptracks?.track || []).map((track) => ({
			name: track.name,
			artist: track.artist.name,
			playcount: parseInt(track.playcount || "0"),
			image:
				track.image.find((img) => img.size === "large")?.[
					"#text"
				] || track.image[0]?.["#text"],
			url: track.url,
		}));

		return Response.json({
			period,
			limit,
			tracks,
		});
	} catch (error) {
		console.error("Failed to fetch top tracks:", error);
		return Response.json(
			{ error: "Failed to fetch top tracks" },
			{ status: 500 }
		);
	}
}
